import { animate, AnimationEntryMetadata, Component, state, style, transition, trigger } from '@angular/core';
import { DomSanitizationService, SafeHtml } from '@angular/platform-browser';

@Component({
  moduleId: module.id,
  selector: 'app-dynamic-html',
  templateUrl:'dynamic-html.component.html',
  directives: [],
  styleUrls: ["dynamic-html.component.css"],

  animations: [
    trigger('expandHeight', [
      state('void', style({
        height: 0
      })),
      state('*', style({
        height: '*'
      })),
      transition('void <=> *', animate(`300ms ease`))
    ])
  ]
})
export class DynamicHtmlComponent  {


  protected expanded: boolean = false;

  constructor(
    private domSanitizationService: DomSanitizationService
  ) {
    //this.name = 'Angular2 (Release Candidate!)'
  }

  toggleExpanded(): void {
    this.expanded = !this.expanded;
  }

  getHtml() {
    return this.domSanitizationService.bypassSecurityTrustHtml(`<div class="dynamic"><b><span style="font-size: 40px">Hel</span>lo</b>> world</div>`);
  }
}
